<script setup lang="ts">
    import { useDisplay } from 'vuetify';
    import { useAppStore } from '#imports';

    /* Store variables */
    const { brandIcon, isMenu, isMobile, siteName, tagLine } = storeToRefs(useAppStore());

    /* Private variables */
    const _display = useDisplay();

    isMobile.value = computed(() => { return _display.mobile.value; });
</script>

<template>
    <v-app-bar>
        <template v-slot:prepend>
            <v-expand-x-transition>
                <div v-show="isMobile">
                    <v-app-bar-nav-icon
                        color="primary"
                        style="background: transparent; "
                        @click="isMenu = !isMenu" />
                </div>
            </v-expand-x-transition>
        </template>
        <v-app-bar-title :class="isMobile ? 'ml-0' : 'ml-3'">
            <div>
                <p class="brand-font color-primary mb-n1 d-flex align-end">
                    <v-icon :icon="brandIcon" color="primary" />
                    {{ siteName }}
                </p>
                <p class="text-caption color-secondary font-italic">{{ tagLine }}</p>
            </div>
        </v-app-bar-title>
        <LoginPseudoRole />
        <SiteSettings />
        <LoginLogInOutBtn />
    </v-app-bar>
</template>